import React from 'react';
import { Helmet } from 'react-helmet';

const BlogDetails = (props) => {
  const data = props.location.state;
  return (
    <div className='container blog-details'>
      <Helmet>
        <title>Blogs - Ginnovation Club</title>
      </Helmet>
      <img
        src='https://newevolutiondesigns.com/images/freebies/robot-facebook-cover-16.jpg'
        alt='blog'
      />
      <header>
        <div
          className='profile-img'
          style={{
            backgroundImage:
              'url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSzIiSQ9vLEJPym-UMILKt7v-XdHSQ4qLlIQA&usqp=CAU")',
          }}
        ></div>
        <h4>Ashutosh Bisoyi</h4>
        <p>Created on 12/02/21</p>
      </header>
      <hr />
      <div className='blog-content'>
        <header>
          <h1>{data.title}</h1>
          <h4>{data.subtitle}</h4>
        </header>
        <p>{data.content}</p>
      </div>
    </div>
  );
};

export default BlogDetails;
